<template>
  <section class="benefits">
    <h2>Por que fazer Pilates?</h2>
    <img class="benefits__img" src="static/man-and-woman.jpg" alt="" />
    <p>
      Fazer Pilates  muito mais do que exercitar o corpo e realizar alguns
      exerccios. Poucos sabem, mas o Mtodo surgiu com o propsito de conectar
      corpo e mente, ou seja, no  um exerccio qualquer.  chamado de Mtodo
      pelo processo de construo de movimentos, fazendo com que o cliente sinta
      seu corpo presente e construa seus movimentos passando por todos os nveis
      do Pilates (bsico, intermedirio e avanado).  um Mtodo que QUALQUER
      pessoa pode fazer, desde criana at a terceira idade. Promove
      concentrao, aumenta a flexibilidade, fortalecimento muscular, alvio de
      dores e um complexo infinito de benefcios.
    </p>
    <div class="benefits__bar"></div>
  </section>
</template>
<script>
export default {};
</script>
<style scoped>
.benefits {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 2rem;
}
.benefits h2 {
  color: var(--cinza-logo);
  font-family: var(--titulo);
  font-size: 2rem;
  text-align: center;
  width: 100%;
}
.benefits__img {
  object-fit: cover;
  width: 90%;
}
.benefits p {
  font-family: var(--texto);
}
.benefits__bar {
  background-color: var(--rosa-logo);
  display: flex;
  height: 1rem;
  width: 100%;
}
@media screen and (max-device-width: 900px) {
  .benefits {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .benefits p {
    font-size: 2.8em;
  }
  .benefits h2 {
    font-size: 5em;
  }
}
</style>


